import { EnvService } from 'src/app/services/env.service';
import { User } from 'src/app/models/user';
import { take } from "rxjs/operators";
import { AnswerComponent } from "./../answer/answer.component";
import { AlertService } from "src/app/services/alert.service";
import { IonRefresher } from "@ionic/angular";
import { PublicQuestionAndAnswer } from "./../../models/public-question-and-answer";
import { PublicQuestionAndAnswersService } from "./../../services/public-question-and-answers.service";
import { ActivatedRoute } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: "app-answers",
  templateUrl: "./answers.page.html",
  styleUrls: ["./answers.page.scss"],
})
export class AnswersPage implements OnInit {
  questionId: number = null;
  roleId = null;
  enableNewAnswer = false;
  userId = null;
  question: PublicQuestionAndAnswer = null;
  refresher: IonRefresher = null;
  defaultBackUrl = [];
  getName = User.getName;
  constructor(
    private route: ActivatedRoute,
    private qaService: PublicQuestionAndAnswersService,
    private alert: AlertService,
    private env:EnvService
  ) {}

  ngOnInit() {
    this.questionId = this.route.snapshot.params.questionId;
    this.roleId = this.route.snapshot.params.roleId;
    this.userId = this.route.snapshot.params.userId;
    this.defaultBackUrl = ["/questions-and-answers",this.roleId,this.userId];
    if(this.roleId == this.env.PROVIDER_ROLE_ID)
    {
      this.enableNewAnswer = true;
    }
    this.qaService.questionWithAnswersShared.reset();
    this.qaService.questionWithAnswersShared
      .getUpdateNotifier(this)
      .subscribe((result) => {
        this.question = this.qaService.questionWithAnswersShared.resource();
        if (this.refresher) {
          this.refresher.complete();
        }
      });

    this.qaService.questionWithAnswersShared.request(this.questionId);
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
  }
  refresh(ev) {
    this.refresher = ev.target;
    this.qaService.questionWithAnswersShared.request(this.questionId);
  }
  answer() {
    this.alert
      .presentModal(AnswerComponent, { questionId: this.questionId }, true)
      .pipe(take(1))
      .subscribe();
  }
}
